
@font-face {
    font-family: ff;
    src: url(/src/fonts/YsabeauSC-Regular.ttf);
  }


html, body{
    width: 100%;
    height: 100%;
    margin: 0;
    background-color: #4919E0;
}

#main-container{
    width: 100%;
    height: 100%;
    color: white;
}

#background-logo{
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 2;
    overflow: hidden;
    font-family: ff;    
    transform-origin: center;    
    top: 50%; right: 50%;
    transform: translate(50%,-50%);
    background-image: linear-gradient(to bottom, #4919E0 , #2A113A);            
}

.background-logo-letters{
    font-size: 130vw;
    color: white;   
    position: relative;    
    top: 50%; right: 50%;    
    transform: translate(-0%,-50%) rotate(-10deg);    
    margin: 0;
    opacity: 0.2;
}

#home-page-container{
    display: grid;
    width: 100%;
    height: 100%;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 3vh repeat(6, 1fr);
    position: relative;
    font-family: ff;
    z-index: 3;    
    overflow-x: hidden;
}


#logo{
    font-size: 1vw;
    display: flex;
    grid-column: 1 / 6;
    grid-row: 2 / 2;
    margin-left: 5vw;
    gap: 10px;
    align-self: center;
}

#company-name{
    align-self: center;
    color: #FFFFFF;
}

#logo2{
    position: sticky;
    top: 20px;
    z-index: 4;
    display: flex;
    width: 25vw;
    justify-content: space-evenly;
    font-size: 1vw;
}

#description{
    grid-column: 9 / 13;
    grid-row: 2 / 2;
    font-size: 1.4vw;
}

#description p{
    text-align: center;
}

#description p:nth-child(2){
    text-align: center;
    margin: auto;
    width: 60%;
}

#catchphrase{
    grid-column: 2 / 12;
    grid-row: 7 / 7;
    font-size: 6vw;
    text-align: center;
}

#catchphrase span{
    border: 3px solid white;
    display: inline-block;
    width: 4vw;
    translate: 0px -1vw;
}

#catchphrase br{
    display: none;
}

.logo-divider{
    border: white solid 2px;
    rotate: 8deg;
    height: 5vw;
    margin: 0px 0px;
}

.logo-dots{
    color: white;
    font-size: 4vw;
    font-weight: bold;
}

#directory{
    position: relative;
    background-color: transparent;
    z-index: 3;
    font-family: ff;
}

#directory-container{
    font-size: 4vw;
    width: 100%;
    height: 100%;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-image: linear-gradient(to top, #4919E0 , #2A113A);    
}

#container-list{
    margin: 0;
    padding: 0;
    list-style-type: none;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: end;
}

.list{
    margin-top: 25vw;
}

.list-item{
    width: 65%;
    margin: 0px 70px 0 0;
    border-top: 2px solid white;
}

.list-item:nth-child(5){
    border-bottom: 2px solid white;    
}

.list-digits {
    font-size: 1.5vw;
    margin-right: 100%;
}

.list-item p{
    margin: 0;
    font-size: 6vw;
}

.list-link{
    font-size: 1.5vw;
    margin-left: 29vw;
    text-decoration: none;
    color: white;
}

#footer{
    text-align: center;
    border: 2px solid white;
    border-radius: 20px;
    margin: 150px auto;
    width: 70%;
    font-size: 7vw;
}

#footer a{
    display: inline-block;
    padding: 40px 0;
    text-decoration: none;
    color: white;
}

@media screen and (max-width: 480px) {
    
    #main-container{
        width: 100%;
        height: 100%;
        color: white;
    }

    #home-page-container{
        display: grid;
        width: 100%;
        height: 100%;
        grid-template-columns: 100%;
        grid-template-rows: repeat(3, 1fr);
        position: relative;
        z-index: 3;    
    }    

    #logo{
        font-size: 3vw;
        display: flex;
        justify-content: center;
        grid-column: 1 / 1;
        grid-row: 1 / 1;
        margin-left: 0;
    }

    #directory{
        overflow-x: hidden;
        height: 170% !important;
    }

    #logo2{
        display: none;
    }

    #description{
        grid-column: 1 / 1;
        grid-row: 3 / 3;
        font-size: 6vw;
    }

    #description p:nth-child(2){
        text-align: center;
        margin: 0;
        width: 100%;
    }

    .logo-divider{
        border: white solid 1px;
        rotate: 8deg;
        height: revert;
        margin: 0px 0px;
    }

    .logo-dots{
        color: white;
        font-size: 14vw;
        font-weight: bold;
    }

    #catchphrase{
        grid-column: 1 / 1;
        font-size: 13vw;
        grid-row: 2 / 2;
        text-align: center;
    }

    #catchphrase span{
        border: 6px solid white;
        display: inline-block;
        width: 14vw;
        translate: 0px -1vw;
    }

    #catchphrase br{
        display: inline;
    }

    #directory{
        width: 100%;
        height: 150%;
    }

    .list-item{
        width: 90%;
        border-top: 2px solid white;
        border-bottom: 2px solid white;    
        margin: auto;
        padding: 15px 0;        
    }
    
    .list-digits {
        margin-right: 100%;
        font-size: 5.5vw;
    }
    
    .list-item p{
        margin: 0;
        font-size: 10vw;
    }
    
    .list-link{
        font-size: 5.5vw;
        margin-left: 40vw;
    }
    
    #footer{
        text-align: center;
        border: 2px solid white;
        border-radius: 10px;
        margin: 90px auto;
        font-size: 10vw;
        width: 90%;
    }

  }